import db from 'libs/db';
import React from 'react';
import { toast } from "react-toastify";
import AdminServices from 'services/api/AdminServices';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import Tabs from 'views/mobileView/components/atoms/Tabs';
import HomePortfolioCard from "views/mobileView/components/molecules/Cards/HomePortfolioCard";
import { HiOutlineExternalLink, HiOutlineHome } from 'react-icons/hi';
import { IoEllipsisVerticalCircleOutline } from 'react-icons/io5';
import FundCard from "views/mobileView/screens/mintboxpro/PortfolioScreen/cards/FundCard";
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import FundConstituents from 'views/mobileView/components/molecules/Cards/FundConstituents';
import TransactionCard from './cards/TransactionCard';
import Modal from 'react-bootstrap/Modal';
import SIPs from './cards/SIPs';

export default function MintboxScreen(props) {

  const [loading, setLoading] = React.useState(true);
  const [showMainModal, setShowMainModal] = React.useState(false)

  const [portfolio, setPortfolio] = React.useState({});
  const [portfolioPerformance, setPortfolioPerformance] = React.useState({});
  const [assetAllocation, setAssetAllocation] = React.useState([]);
  const [portfolioXIRR, setPortfolioXIRR] = React.useState({});
  const [transactions, setTransactions] = React.useState([]);
  const [user, setUser] = React.useState({});

  const MINTBOXID = props.location.state.MINTBOXID;
  // const FUNDDATA = portfolio?.MintboxConsolidated?.find(f => f.MintboxId === MINTBOXID && f.GoalId === 0);
  const FUNDDATA = props.location.state.FUNDDATA;
  const CONSTITUENTS = portfolio?.MintboxETF?.filter(f => f.MintboxId === MINTBOXID && f.GoalId === 0 && f.MintboxETFBalance !==0).map((item) => {
    item.CurrentWeightage = item.MintboxETFBalance * 100 / FUNDDATA.MintboxBalance
    return item
})
  
  React.useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    setLoading(true)
    try {
      db.get(db.model.user).then(async (p) => {
        setUser(p.userData);
        await getPortfolio(p.userData.id)
        await getAssetAllocation(p.userData.id)
        await getPortfolioXIRR(p.userData.id)
        await getPortfolioPerformance(p.userData.id)
        await getTransactions(p.userData.id)
        // await getMintboxConstituentsById()
        setLoading(false)
      })
    } catch (error) {
      toast.error(error);
      setLoading(false)
    }
  };

  // const getMintboxConstituentsById = async () => {
  //   let res = await AdminServices.getMintboxConstituentsById(MINTBOXID).then(res => res)
  //   if (res.type) {
  //     console.log("Get mintbox constituents API", res.data)
  //     setMintboxConstituents(res.data)
  //   } else {
  //     toast.error("Error")
  //   }
  // }

  const getPortfolio = async (id) => {
    db.get(db.model.portfolio).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolio(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio API :", aa)
          setPortfolio(aa)
          await (db.model.portfolio, JSON.stringify(aa))
        }
      } else {
        setPortfolio(p)
      }
    })
  }

  const getPortfolioPerformance = async (id) => {
    db.get(db.model.portfolioPerformance).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioPerformance(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolioPerformance API :", aa)
          setPortfolioPerformance(aa)
          await db.set(db.model.portfolioPerformance, JSON.stringify(aa))
        }
      } else {
        setPortfolioPerformance(p)
      }
    })
  }

  const getAssetAllocation = async (id) => {
    db.get(db.model.assetAllocation).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioAssetAllocation(id).then(res => res);
        if (res.type === true) {
          const aa = res.data;
          console.log("Get portfolio asset allocation API :", aa)
          formatAssetAllo(aa)
          await db.set(db.model.assetAllocation, JSON.stringify(aa))
        }
      } else {
        formatAssetAllo(p)
      }
    })
  }
  const formatAssetAllo = (aa) => {
    const assetClasses = [
      {
        name: 'Indian Equity',
        color: cssVar.getPropertyValue('--indianEquity')
      },
      {
        name: 'Gold',
        color: cssVar.getPropertyValue('--gold')
      },
      {
        name: 'US Equity',
        color: cssVar.getPropertyValue('--internationalEquity')
      },
      {
        name: 'Debt',
        color: cssVar.getPropertyValue('--debt')
      },
      {
        name: 'Cash',
        color: cssVar.getPropertyValue('--cash')
      },
      {
        name: 'Silver',
        color: cssVar.getPropertyValue('--silver')
      }
    ];
    aa = aa.filter(f => f.MintboxId === MINTBOXID)
    let percentArr = []
    const totalAllocation = aa.reduce((total, r) => total + r.AssetBalance, 0)
    if (aa.length !== 0) {
      assetClasses.forEach(a => {
        const subTotal = aa.filter(f => f.AssetClass === a.name).reduce((total, r) => total + r.AssetBalance, 0) * 100 / totalAllocation
        if (subTotal) {
          percentArr.push({ ...a, y: Number(subTotal.toFixed(2)) })
        }
      })
    }
    setAssetAllocation(percentArr)
  }

  const getPortfolioXIRR = async (id) => {
    db.get(db.model.portfolioXirr).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioXIRR(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          const aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio XIRR API :", aa)
          setPortfolioXIRR(aa)
          await db.set(db.model.portfolioXirr, JSON.stringify(aa))
        }
      } else {
        setPortfolioXIRR(p)
      }
    })
  }

  const getTransactions = async (id) => {
    db.get(db.model.transactions).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getTransactions(id).then(res => res);
        if (res.type === true) {
          const aa = res.data;
          console.log("Get transactions API :", aa)
          setTransactions(aa)
          await db.set(db.model.transactions, JSON.stringify(aa))
        }
      } else {
        setTransactions(p)
      }
    })
  }

  const onPortfolioRefresh = async () => {
    await db.remove(db.model.portfolio);
    await db.remove(db.model.portfolioXirr);
    await db.remove(db.model.portfolioPerformance);
    await db.remove(db.model.assetAllocation);
    await db.remove(db.model.transactions)
    loadData();
  }

  const Funds = () => {
    return (
      <div style={{ marginTop: "2%" }}>
        {portfolio?.MintboxETF?.filter(f => f.MintboxId === MINTBOXID && f.GoalId === 0 && f.MintboxETFBalance !==0)?.map((data) => (
          <FundCard key={data.FundCode}
            onClick={() => props.history.push({
              pathname: '/portfolio/etfScreen',
              state: { FUNDCODE: data.FundCode, MINTBOXID: MINTBOXID, TYPE: 'MintboxETF', FUNDDATA:data }
            })}
            fundName={data.ETFName}
            AMCName = {data.AMCName}
            balance={data.MintboxETFBalance}
            previousBalance={portfolio?.PrevMintboxETF?.find(m => m.MintboxId === MINTBOXID && m.FundCode === data.FundCode)?.PrevMintboxETFBalance}
            xirr={Object.values(portfolioXIRR?.MintboxETFXIRR || {}).find(m => m.MintboxId === MINTBOXID && m.FundCode === data.FundCode)?.XIRR}
            type = {1}
          />
        ))}
      </div>
    )
  }

  // const AssetAllocation = () => {
  //   var options = {
  //     chart: {
  //       type: 'pie',
  //       style: {
  //         fontFamily: 'DMSans-Regular',
  //         color: '#333',
  //         fontWeight: 'normal'
  //       },
  //       backgroundColor: "transparent",
  //       plotBackgroundColor: null,
  //       plotBorderWidth: null,
  //       plotShadow: false,
  //       height: 220
  //     },
  //     title: {
  //       text: ''
  //     },
  //     scrollbar: {
  //       enabled: false
  //     },
  //     exporting: {
  //       enabled: false
  //     },
  //     legend: {
  //       itemStyle: {
  //         color: "#6c6c6c",
  //         fontSize: 14,
  //         fontWeight: 'normal',
  //         fontFamily: 'DMSans-Regular'
  //       }
  //     },

  //     plotOptions: {
  //       pie: {
  //         allowPointSelect: true,
  //         cursor: 'pointer',
  //         dataLabels: {
  //           enabled: false
  //         },

  //         showInLegend: true,
  //         size: 150,
  //       }
  //     },
  //     credits: {
  //       enabled: false
  //     },
  //     tooltip: {
  //       valueSuffix: '%'
  //     },
  //     legend: {
  //       enabled: true,
  //       align: 'right',
  //       verticalAlign: 'top',
  //       layout: 'vertical',
  //       x: -30,
  //       y: 30,
  //       itemMarginBottom: 10
  //     },
  //     series: [
  //       {
  //         data: assetAllocation
  //       }
  //     ]
  //   };
  //   return (
  //     <div style={{ margin: "4%" }}>
  //       <HighchartsReact
  //         highcharts={Highcharts}
  //         options={options}
  //       />
  //       <div style={{ fontSize: '14px', fontWeight: "400", color: "#6c6c6c", marginTop: '2%' }}>Last reviewed: 1st March, 2022</div>
  //       <div style={{ fontSize: '14px', fontWeight: "400", color: "#6c6c6c", marginTop: '2%' }}>Next review: 1st September, 2021</div>
  //       <div style={{ fontSize: '14px', fontWeight: "500", color: "#294C88", marginTop: '4%' }}>Understand review and rebalancing <AiOutlineRight size={14} /></div>
  //     </div>
  //   )
  // }

  const Allocation = () => {
    return <FundConstituents fundConstituents={CONSTITUENTS} type='portfolio' />
  }

  const Transactions = () => {
    return (
      <div style={{ marginTop: "2%" }}>
        {
          transactions.filter(t => t.FundType === 1 && t.MintboxId === MINTBOXID).reverse().map((trx,i) => {
            return <TransactionCard trx={trx} i={i} type={3}/>
          })
        }
      </div>
    )
  }

  const keyTitles = [
    { title: 'Funds', view: Funds() },
    // { title: 'Asset allocation', view: AssetAllocation() },
    { title: 'Allocation', view: Allocation() },
    { title: 'SIPs', view: SIPs({navigation:props.history,TYPE:3, FUNDCODE:MINTBOXID, FUNDDATA: FUNDDATA}) },
    { title: 'Transactions', view: Transactions() }
  ];

  const MainModal = () => {
    const [selectedOption, setSelectedOption] = React.useState()

    const onConfirm = () => {
      switch (selectedOption) {
        case 'redeem': {
          props.history.push({
            pathname: '/portfolio/redeem-mintbox',
            state: { fundData: FUNDDATA, constituents: CONSTITUENTS }
          })
          setShowMainModal(false);
          break;
        }
        case 'sip': {
          setShowMainModal(false);
          props.history.push({
            pathname: '/portfolio/confirm-invest',
            state: { fundData: FUNDDATA, type: 3, sip: true }
          })
          break;
        }
        default: setShowMainModal(false)
      }
    }
    return (
      <Modal
        className="bottom-drawer"
        show={showMainModal}
        onHide={() => setShowMainModal(false)}
        keyboard={false}
      >
        <Modal.Body>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="sip" id="sip" />
            <label className="bodyTextDark radio-label" htmlFor="sip">Start a SIP</label>
          </div>
          <div className="radio-item" style={{ marginTop: "6%" }}>
            <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="redeem" id="redeem" />
            <label className="bodyTextDark radio-label" htmlFor="redeem" style={{ color: cssVar.getPropertyValue('--peach') }}>Redeem</label>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="outlineButton" onClick={() => setShowMainModal(false)}>
            Close
          </button>
          <button className="solidButton" onClick={() => onConfirm()}>
            Confirm
          </button>
        </Modal.Footer>
      </Modal>
    )
  }

  if (loading) {
    return (
      <ActivityLoaderMintbox />
    );
  }

  return (
    <div className='mainContainer'>
      <MainHeader
        navigation={props.history}
        header="mintbox"
      />
      <div className="dataContainer px-0">
        <div style={{ margin: "4% 4% 2%" }}>
          <div style={{ display: "flex", gap: "4%" }}>
            <MintboxIcon risk={portfolio.MintboxConsolidated?.find(f => f.MintboxId === MINTBOXID)?.RiskLevel} width='40px' height='40px' />
            <div className='header1dark' style={{ flex: 1 }}>
              {FUNDDATA?.MintboxName}
            </div>
            <HiOutlineExternalLink size={24} className='secondaryGreen' onClick={() => props.history.push("/mintbox-detail/" + MINTBOXID)} style={{alignSelf:'center'}}/>
          </div>
        </div>
        <HomePortfolioCard
          navigation={props.history}
          balance={portfolio.MintboxConsolidated?.find(f => f.MintboxId === MINTBOXID && f.GoalId === 0)?.MintboxBalance}
          previousBalance={portfolio.PrevMintboxConsolidated?.find(f => f.MintboxId === MINTBOXID && f.GoalId === 0)?.PrevMintboxBalance}
          // xirr={portfolioXIRR.MintboxXIRR[MINTBOXID]?.XIRR}
          xirr={Object.values(portfolioXIRR?.MintboxXIRR || {}).find(m => m.MintboxId === MINTBOXID && m.GoalId === 0)?.XIRR}
          onRefresh={() => { onPortfolioRefresh() }}
          growthChartData={portfolioPerformance?.Mintbox?.filter(f => f.MintboxId === MINTBOXID)?.map(d => [new Date(d["NavDateStr"]).getTime(), d["Balance"]])}
        />
        <Tabs tabsObject={keyTitles} color={cssVar.getPropertyValue('--darkGreen')} />
      </div>
      <div className='bottom-nav'>
        <div className='bn-tab' onClick={() => { props.history.push('/dashboard') }}>
          <HiOutlineHome size={20} className='footerIcon' />
          <div className='footerText'>Home</div>
        </div>
        <div className='bn-tab' style={{ flex: 3 }}>
          <button
            className='solidButton'
            onClick={() => props.history.push({
              pathname: '/portfolio/confirm-invest',
              state: {
                fundData: FUNDDATA,
                type: 3
              }
            })}>
            Invest more
          </button>
        </div>
        <div className='bn-tab'>
          <IoEllipsisVerticalCircleOutline
            size='48'
            color={cssVar.getPropertyValue('--secondaryGreen')}
            onClick={() => setShowMainModal(true)} />
        </div>
      </div>
      <MainModal />
    </div>
  );
}
