import React from 'react';
import cssVar from 'views/mobileView/styles';
import AMCIcon from '../../atoms/AMCIcon';

function FundConstituents({ fundConstituents, type=''}) {

  const displayAssetClass = (id) => {
    const assetClasses = [
      {
        name: 'Indian Equity',
        color: cssVar.getPropertyValue('--indianEquity')
      },
      {
        name: 'Gold',
        color: cssVar.getPropertyValue('--gold')
      },
      {
        name: 'Foreign Equity',
        color: cssVar.getPropertyValue('--internationalEquity')
      },
      {
        name: 'Debt',
        color: cssVar.getPropertyValue('--debt')
      },
      {
        name: 'Cash',
        color: cssVar.getPropertyValue('--cash')
      },
      {
        name: 'Silver',
        color: cssVar.getPropertyValue('--silver')
      }
    ];
    if (fundConstituents) {
      const fundsArray = fundConstituents.filter(fund => fund.AssetClassId === id)
      const totalAllocation = fundsArray.reduce((total, fund) => Number(fund.CurrentWeightage) + total, 0);
      const assetClassName = assetClasses[id - 1].name
      const assetColor = assetClasses[id - 1].color

      if (fundsArray.length === 0) { return <></> }
      else return (
        <>
          {
            type === 'only-constituents'?
            <></>
            :
          <div className='header1dark' style={{ display: "flex",gap:'4%',alignItems:'center',padding: type==='goal-recom'?'3% 0':'5% 0 1% 0',  margin:'0 4%',borderBottom: type === 'goal-recom'? '1px solid '+cssVar.getPropertyValue('--light') : 'none' }}>
            {
              type === 'goal-recom'?
                <div style={{width:'12px',height:'12px',borderRadius:'50%',backgroundColor: assetColor}}></div>
                :
                <></>
            }
            <div style={{ flex: 1, color: assetColor }} className='header1dark'>{assetClassName}</div>
            <div style={{ padding: "0 4%",color: assetColor }}>{Number(totalAllocation).toFixed(1)}%</div>
          </div>
          }
          {
            type ==='goal-recom'? 
              <></>
              :
              fundsArray.map((fund,i) => {
                return (
                  <div key={i} style={{ display: "flex", padding: "3% 4%",borderBottom:"1px solid "+cssVar.getPropertyValue('--light') }}>
                    <div style={{ flex: 1, display: 'flex', gap: "4%" }}>
                      <AMCIcon amcName={fund.AMCName} height='24px' width='24px' />
                      <div className='bodyTextDark' style={{ flex: 1 }}>{fund.ETFName}</div>
                    </div>
                    <div className='header1dark' style={{ padding: "0 4%" }}>{Number(fund.CurrentWeightage).toFixed(1)}%</div>
                  </div>
                )
              })
          }
        </>
      )
    }
  }
  return (
    <>
      <div style={{ display: "flex" , padding:"6% 4% 1% 4%"}}>
        <div className='bodyTextLight' style={{ flex: 1 }}>Asset class/ETF</div>
        <div className='bodyTextLight'>Allocation{type==='portfolio'?'':'*'}</div>
      </div>
      {displayAssetClass(1)}
      {displayAssetClass(2)}
      {displayAssetClass(3)}
      {displayAssetClass(4)}
      {displayAssetClass(5)}
      {displayAssetClass(6)}
      {
        type==='portfolio'?
          <></>
          :
          type === 'goal-recom'?
          <div className='bodyTextLight' style={{ padding: "4%" }}>* Allocations are reviewed periodically to
          maximize the chances of achieving this goal.</div>
          :
          <div className='bodyTextLight' style={{ padding: "4%" }}>* Allocation will change in the future to adjust portfolio risk as per our proprietary investment logic.</div>
      }
    </>
  )
}

export default FundConstituents